___CSS_LOADER_EXPORT___.push([module.id, `body {
  font-family: 'Poppins', sans-serif;
  margin: 0;
  padding: 0;
}

/* ! Navbar Style */
.navbar-brand img {
  width: 217px;
  height: 60px;
}

/* ! Article Style */
article {
  padding: 1.5em 2em;
  font-size: 1.1em;
  line-height: 1.5;
  margin: 1.5em 0;
}

.article-title {
  text-align: center;
  font-size: 1.5em;
  margin-top: 0;
}

.article-img {
  width: 100%;
  display: block;
  border-radius: 1.7em;
  margin-bottom: 1.5em;
  margin-left: auto;
  margin-right: auto;
}

.article-content {
  text-align: justify;
}

.article-source {
  text-align: justify;
  font-size: 0.9em;
}

.article-source h3 {
  margin-bottom: 0;
}

.article-source ul {
  padding-left: 1em;
  margin: 0;
}

.article-content h2 {
  font-size: 1.2em;
}

.article-content p {
  text-indent: 1.5em;
}

.article-content ol {
  padding-left: 1.5em;
}

.article-content strong {
  display: block;
  margin: 1em 0 0.5em 0;
}

.article-content span {
  display: block;
}

@media screen and (min-width: 767px) {
  article {
    padding: 1.5em 4em;
    font-size: 1.2em;
  }

  .article-title {
    font-size: 2em;
  }

  .article-content h2 {
    font-size: 1.3em;
  }

  .article-content ol {
    padding-left: 1.5em;
  }
}

@media screen and (min-width: 1200px) {
  article {
    padding: 1.5em 5em;
    font-size: 1.4em;
  }

  .article-img {
    width: 80%;
  }

  .article-title {
    padding: 0 3em;
  }

  .article-content {
    padding: 0 4em;
  }

  .article-source {
    padding: 0 5em;
  }
}
